import * as React from 'react'
import { useState } from 'react'
//import { Link } from 'gatsby'

const arr = [1,2,4,5,6]

const SmBox = () => {

    const [val, setVal] = useState(arr)

    return (
        
        <div>
            <button onClick={() => setVal([...val, val[val.length - 1] + 1 ])}> click me </button>
            { val.map( (v, i) => <h5 key={i}>About Me {v}</h5> ) }
        </div>
    )

}

export default SmBox